<template>
	<div :class="collapse?'collapsed':''" class="body">
		<div class="wrapper">
            <!-- HEADER -->
			<header class="main-header">
				<!-- LOGO -->
				<router-link to="login" class="logo">
					<span v-if="collapse" class="logo-mini"><i class="el-icon-fa-podcast"></i></span>
					<span v-else class="logo-lg"><i class="el-icon-fa-podcast" style="font:16px"></i> {{app}}</span>
				</router-link>
				<nav class="navbar navbar-static-top">
					<a @click.prevent="toggleSidebar" class="sidebar-toggle"><span class="sr-only">缩小边栏</span></a>
					<div class="navbar-custom-menu">
						<ul class="nav navbar-nav">
							<li class="dropdown user user-menu">
								<el-dropdown trigger="hover">
                                    <!-- TODO: 添加默认头像 -->
									<a class="el-dropdown-link userinfo-inner"><img :src="account.avatar" class="user-image" /> {{account.name}}</a>
									<el-dropdown-menu slot="dropdown">
										<el-dropdown-item><i class="fa fa-vcard"></i> <router-link to="/home/me/profile">个人资料</router-link></el-dropdown-item>
										<el-dropdown-item><i class="fa fa-comments"></i> <router-link to="/home/me/event">通知提醒</router-link></el-dropdown-item>
										<el-dropdown-item divided><a @click.prevent="onLogout"><i class="fa fa-window-close"></i> 退出登录</a></el-dropdown-item>
									</el-dropdown-menu>
								</el-dropdown>
							</li>
						</ul>
					</div>
				</nav>
			</header>
            <!-- HEADER END -->
            <!-- SIDEBAR START -->
            <!-- <aside class="main-sidebar" :class="collapse?'menu-collapsed':'menu-expanded'"> -->
            <aside class="main-sidebar">
                <section class="sidebar">
                <!-- USER PANEL -->
                    <div class="user-panel">
                        <div class="pull-left image">
                            <img :src="account.avatar" alt="User Image" class="img-circle">
                        </div>
                        <div class="pull-left info">
                            <p>{{account.name}}</p>
                            <a href="#">[ {{account.nickname}} ]</a>
                        </div>
                    </div>
                    <!-- TODO: WEATHER -->
                    <!-- <p class="weather">{{weather.date}} {{weather.week}} {{weather.weather}} <a class="btn" @click="fetchWeather"><i class="fa fa-refresh"></i></a></p> -->
                    <!--导航菜单-->
                    <el-menu :default-active="$route.path" :default-openeds="openeds" unique-opened router :collapse="collapse">
                        <template v-for="(item, index) in menu">
                            <el-submenu v-if="'children' in item" :index="item.path">
                                <template slot="title">
                                    <i :class="'el-icon-fa-'+item.icon"></i>
                                    <span slot="title"> {{item.name}}</span>
                                </template>
                                <el-menu-item-group>
                                    <el-menu-item v-for="child in item.children" :index="child.path" :key="child.path">
                                        <router-link :to="child.path">{{child.name}}</router-link>
                                    </el-menu-item>
                                </el-menu-item-group>
                            </el-submenu>
                            <el-menu-item v-else :index="item.path">
                                <i :class="'el-icon-fa-'+item.icon"></i>
                                <span slot="title"> {{item.name}}</span>
                            </el-menu-item>
                        </template>
                    </el-menu>
                </section>
            </aside>
            <!-- SIDEBAR END -->
            <!-- CONTENT START -->
            <div class="content-wrapper">
                <section class="content-header">
                    <strong class="title">{{$route.meta.name}}</strong>
                    <el-breadcrumb separator="/" class="breadcrumb-inner">
                        <el-breadcrumb-item :to="{name:'home'}"><i class="el-icon-fa-home"></i></el-breadcrumb-item>
                        <el-breadcrumb-item v-for="route in $route.matched" v-if="route.path != '/home'" :to="route.path" :key="route.path">{{ route.meta.name }}</el-breadcrumb-item>
                    </el-breadcrumb>
                </section>
                <el-col :span="24" class="content">
                    <!-- <transition name="fade" mode="out-in"> -->
                        <router-view></router-view>
                    <!-- </transition> -->
                </el-col>
            </div>
            <!-- CONTENT END -->
		</div>
	</div>
</template>
<script>
// 约定菜单循环的key使用 router.name
// 使用这个值设置默认打开菜单项
export default {
  data () {
    return {
      collapse: window.localStorage.getItem('wemesh_sidebar') === 'closed',
      app: ' 胶东医院信息化系统'
    }
  },
  methods: {
    toggleSidebar () {
      this.collapse = !this.collapse
      this.collapse ? window.localStorage.setItem('wemesh_sidebar', 'closed') : window.localStorage.setItem('wemesh_sidebar', 'open')
    },
    goto (path) {
      this.$router.push('/dashboard/' + path)
      window.console.log(path)
    },
    showMenu (i, status) {
      this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' : 'none'
    },
    onLogout () {
      this.$store.dispatch('logout')
    }
  },
  computed: {
    account () {
      return this.$store.state.account
    },
    menu () {
      return this.$store.state.account.menus
    },
    openeds () {
      return ['/' + this.$route.path.split('/')[1], '/' + this.$route.path.split('/')[2]]
    }
  },
  mounted () {
    let localStorage = window.localStorage.getItem('wemesh_sidebar')
    if (localStorage === null) {
      window.localStorage.setItem('wemesh_sidebar', 'open')
    }
  }
}
</script>


